// @flow
import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import _get from 'lodash/get';
import { withErrorBoundary } from 'components/ErrorBoundary';
import {
  CELL_CLASS,
  DATE_PICKER_ID,
  getCellsProp,
  isElementContains,
  OVERLAY_ID,
} from './helpers';
import _isEmpty from 'lodash/isEmpty';

const useStyles = makeStyles(theme => ({
  cells: {
    width: '100%',
    position: 'absolute',
    left: 0,
    background: theme.palette.primary.light,
  },
}));

type Props = {
  selectingCells: Object,
  slotLengthMinutes: number,
};

const SelectedCells = (
  props: Props,
): React.Element<'div' | typeof React.Fragment> => {
  const classes = useStyles();
  const { selectingCells, slotLengthMinutes } = props;
  const cellsProps = getCellsProp(selectingCells, slotLengthMinutes);

  const onMouseUp = event => {
    event.preventDefault();
    if (_isEmpty(selectingCells)) return;

    const { target } = event;
    if (target.classList.contains(CELL_CLASS)) return;
    if (isElementContains(OVERLAY_ID, target)) return;
    if (isElementContains(DATE_PICKER_ID, target)) return;

    const { startCell } = selectingCells;
    const cell = document.getElementById(startCell.toString());
    if (cell) {
      cell.dispatchEvent(new Event('mouseup', { bubbles: true }));
    }
  };

  React.useEffect(() => {
    window.addEventListener('mouseup', onMouseUp);
    return () => {
      window.removeEventListener('mouseup', onMouseUp);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return <div className={classes.cells} style={cellsProps}></div>;
};

const structuredSelector = createStructuredSelector({
  selectingCells: state => _get(state, 'calendar.selectingCells'),
  slotLengthMinutes: state =>
    _get(state, 'calendar.config.times.slotLengthMinutes'),
});

type PropsWithoutRedux = {};
export default (connect(structuredSelector)(
  withErrorBoundary(React.memo(SelectedCells)),
): React.AbstractComponent<PropsWithoutRedux>);
