// @flow
import { Box, makeStyles, Typography } from '@material-ui/core';
import * as React from 'react';
import type { Element } from 'react';
import { formatCells, displayTime, displayDate } from '../../helpers';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import { withErrorBoundary } from 'components/ErrorBoundary';

const useStyles = makeStyles(theme => ({
  wrapper: {
    ...theme.typography.h4,
    padding: theme.spacing(2, 0),
    backgroundColor: theme.palette.background.gray,
    textAlign: 'center',
  },
}));

const AppointmentData = ({
  selectingCells,
  slotLengthMinutes,
}: Object): Element<'div'> => {
  const classes = useStyles();
  if (_isEmpty(selectingCells)) return <div></div>;
  const { date } = selectingCells;
  const { start, end } = formatCells(selectingCells, slotLengthMinutes);
  const displayStart = displayTime(start);
  const displayEnd = displayTime(end);

  return (
    <div className={classes.wrapper}>
      <Typography variant="h4">{displayDate(date)}</Typography>
      <Box sx={{ mt: 1 }}>
        <Typography variant="h3">
          {displayStart} - {displayEnd}
        </Typography>
      </Box>
    </div>
  );
};

const structuredSelector = createStructuredSelector({
  selectingCells: state => _get(state, 'calendar.selectingCells'),
  slotLengthMinutes: state =>
    _get(state, 'calendar.config.times.slotLengthMinutes'),
});

type PropsWithoutRedux = {};
export default (connect(structuredSelector)(
  withErrorBoundary(React.memo(AppointmentData)),
): React.AbstractComponent<PropsWithoutRedux>);
