// @flow
import type { TQuestion } from '../../../types';
import { Auth } from 'aws-amplify';
import { TYPES } from './constants';
import { formatCells, toMinutes } from '../helpers';
import moment from 'moment';
import 'moment-timezone';
import _get from 'lodash.get';

export const BOOKING_FORMAT = 'YYYY-MM-DDTHH:mm:ss';
const BOOKING_DATE_FORMAT = 'YYYY-MM-DD';
const BOOKING_TIME_FORMAT = 'HH:mm:ss';
export const DATE_PICKER_FORMAT = 'MM/DD/YYYY';
const WEEK_DAYS = 7;

const defaultValues = {
  [TYPES.SIMPLE]: '',
  [TYPES.CHECKBOX]: false,
  [TYPES.MULTI_DROPDOWN]: '',
};

const REPEAT_TYPE = {
  daily: 'daily',
  weekly: 'weekly',
};

export const generateInitialValues = ({ questions }: Object): Object => {
  return questions.reduce(
    (acc, { code, type }) => ({ ...acc, [code]: defaultValues[type] }),
    {
      appointmentType: undefined,
    },
  );
};

export const generateSellerValues = ({
  property,
  selectingCells,
  config,
}: Object): Object => {
  const {
    questions,
    times: { slotLengthMinutes },
  } = config;
  const { start } = formatCells(selectingCells, slotLengthMinutes);
  const date = start.format(DATE_PICKER_FORMAT);
  const address = _get(property, 'PropertyData.FullAddress', '');
  const email = _get(Auth, ['user', 'attributes', 'email'], '');
  return {
    ...generateInitialValues({ questions }),
    FNAME: address,
    EMAIL: email,
    repeat: false,
    repeatType: REPEAT_TYPE.daily,
    startDate: date,
    endDate: date,
  };
};

const prepareQuestionsAnswers = (questions: TQuestion[], values): Object => {
  return questions
    .map(({ code }) => ({ code, string: values[code] }))
    .filter(({ string }) => string);
};

const generateBookingRange = (selectingCells, slotLengthMinutes) => {
  const { startCell, endCell } = selectingCells;
  const { start } = formatCells(selectingCells, slotLengthMinutes);
  const startMinutes = toMinutes(startCell);
  const endMinutes = toMinutes(endCell);
  const duration =
    Math.abs((startMinutes - endMinutes) / slotLengthMinutes) + 1;
  return Array.from(Array(duration), (_, i) => ({
    startsAt: moment(start)
      .add(i * slotLengthMinutes, 'm')
      .format(BOOKING_FORMAT),
  }));
};

export const prepareBookingData = ({
  selectingCells,
  values,
  config,
}: Object): Object => {
  const {
    title,
    questions,
    times: { slotLengthMinutes },
  } = config;
  const { FNAME: name, appointmentType } = values;
  const bookingRange = generateBookingRange(selectingCells, slotLengthMinutes);

  return bookingRange.map(({ startsAt }) => ({
    startsAt,
    title: `${name} and ${title}`,
    tentative: 'NOT_USED',
    timeZone: moment.tz.guess(),
    cancelled: false,
    units: 1,
    services: [appointmentType],
    answers: prepareQuestionsAnswers(questions, values),
  }));
};

export const BUSY_APPOINTMENT = 'busy';

export const getBusyAppointmentType = (
  appointmentTypes: Object[],
): Object | typeof undefined => {
  return appointmentTypes.find(
    ({ name }) => name.toLowerCase() === BUSY_APPOINTMENT,
  );
};

export const prepareSellerBookings = ({
  selectingCells,
  values,
  config,
}: Object): Object[] => {
  const {
    questions,
    times: { slotLengthMinutes },
  } = config;
  const { repeat, repeatType, startDate, endDate } = values;
  const appointmentTypes = _get(config, 'services.items', []);
  const busyAppointmentType = getBusyAppointmentType(appointmentTypes);
  const bookingRange = generateBookingRange(selectingCells, slotLengthMinutes);

  if (!repeat)
    return bookingRange.map(({ startsAt }) => ({
      startsAt,
      title: BUSY_APPOINTMENT,
      tentative: 'NOT_USED',
      timeZone: moment.tz.guess(),
      cancelled: false,
      units: 1,
      services: [busyAppointmentType],
      answers: prepareQuestionsAnswers(questions, values),
    }));

  const mStartDate = moment(startDate);
  const mEndDate = moment(endDate);
  const diff = mEndDate.diff(mStartDate, 'day');
  const module = repeatType === REPEAT_TYPE.weekly ? WEEK_DAYS : 1;

  const days = Array.from(
    Array(Math.floor(diff / module) + 1),
    (_, i) => i * module,
  );

  return days.reduce((bookings, day) => {
    const mDay = moment(startDate).add(day, 'day');
    const bookingsInDay = bookingRange.map(({ startsAt }) => ({
      title: BUSY_APPOINTMENT,
      startsAt: `${mDay.format(BOOKING_DATE_FORMAT)}T${moment(startsAt).format(
        BOOKING_TIME_FORMAT,
      )}`,
      tentative: 'NOT_USED',
      timeZone: moment.tz.guess(),
      cancelled: false,
      units: 1,
      services: [busyAppointmentType],
      answers: prepareQuestionsAnswers(questions, values),
    }));
    return [...bookings, ...bookingsInDay];
  }, []);
};

export const getErrorMessages = (bookings: Object[]): Object => {
  const bookingDates = bookings.map(booking => booking.startsAt).join(', ');
  return { message: `Cannot book at dates: ${bookingDates}` };
};
