// @flow
import * as React from 'react';
import Paper from '@material-ui/core/Paper';
import _get from 'lodash/get';
import Typography from '@material-ui/core/Typography';
import { formatTime, isBusyAppointment } from './helpers';
import { makeStyles } from '@material-ui/core/styles';
import {
  AccessTime as AccessTimeIcon,
  Event as EventIcon,
} from '@material-ui/icons';
import { Box } from '@material-ui/core';
import clsx from 'clsx';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';

const useStyles = makeStyles(theme => ({
  wrapper: {
    padding: theme.spacing(2),
    paddingTop: 0,
  },
  section: {
    marginBottom: theme.spacing(1),
  },
  busy: {
    color: theme.palette.primary.gray,
  },
}));

type Props = {
  appointmentData: Object,
  isBuyer: boolean,
};

const TooltipContent = ({
  appointmentData,
  isBuyer,
}: Props): React.Element<'div'> => {
  const classes = useStyles();
  const { displayDate, displayTime } = formatTime(appointmentData);
  const showBusy = isBuyer && isBusyAppointment(appointmentData);

  return (
    <Paper className={classes.wrapper}>
      <Typography
        variant="h4"
        color="primary"
        className={clsx(classes.section, { [classes.busy]: showBusy })}
      >
        {showBusy ? 'Busy' : _get(appointmentData, 'title', '')}
      </Typography>
      <Box sx={{ mb: 1, display: 'flex', alignItems: 'center' }}>
        <Box sx={{ mr: 0.5, display: 'flex' }}>
          <EventIcon fontSize="small" />
        </Box>
        <Typography variant="h5">{displayDate}</Typography>
      </Box>
      <Box sx={{ mb: 1, display: 'flex', alignItems: 'center' }}>
        <Box sx={{ mr: 0.5, display: 'flex' }}>
          <AccessTimeIcon fontSize="small" />
        </Box>
        <Typography variant="h5">{displayTime}</Typography>
      </Box>
    </Paper>
  );
};

type PropsWithoutRedux = {
  appointmentData: Object,
};

const structuredSelector = createStructuredSelector({
  isBuyer: state => !_get(state, ['calendar', 'property']),
});

export default (connect(structuredSelector)(
  TooltipContent,
): React.AbstractComponent<PropsWithoutRedux>);
