// @flow
import * as React from 'react';
import { WeekView } from '@devexpress/dx-react-scheduler-material-ui';
import { makeStyles, alpha } from '@material-ui/core/styles';
import clsx from 'clsx';
import SelectedCells from './SelectedCells';
import { Box } from '@material-ui/core';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import _get from 'lodash/get';
import { withErrorBoundary } from 'components/ErrorBoundary';
import moment from 'moment';
import { DATE_FORMAT } from './helpers';

const useStyles = makeStyles(theme => ({
  cell: {
    '&&': {
      padding: theme.spacing(1, 0),
      width: '100%',
      display: 'block',
      textAlign: 'center',
    },
  },
  today: {
    backgroundColor: alpha(theme.palette.primary.main, 0.16),
  },
}));

const DayScaleCell = (props: Object): React.Element<typeof React.Fragment> => {
  const classes = useStyles();
  const { today, startDate, selectingDate, isSelecting } = props;
  const hasSelectingCells =
    isSelecting && selectingDate === moment(startDate).format(DATE_FORMAT);

  return (
    <Box
      component="td"
      sx={{
        display: 'table-cell',
        position: 'relative',
        width: '100%',
      }}
    >
      <WeekView.DayScaleCell
        {...props}
        className={clsx(classes.cell, {
          [classes.today]: today,
        })}
      ></WeekView.DayScaleCell>
      {hasSelectingCells && <SelectedCells />}
    </Box>
  );
};

const structuredSelector = createStructuredSelector({
  selectingDate: state => _get(state, 'calendar.selectingCells.date'),
  isSelecting: state => _get(state, 'calendar.isSelecting'),
});

type PropsWithoutRedux = { date: Date };
export default (connect(structuredSelector)(
  withErrorBoundary(React.memo(DayScaleCell)),
): React.AbstractComponent<PropsWithoutRedux>);
