// @flow
import * as React from 'react';
import { WeekView } from '@devexpress/dx-react-scheduler-material-ui';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import _get from 'lodash/get';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { CELL_CLASS, isCellActive } from './helpers';
import { beginSelectingCell, addSelectedCell } from './redux/Actions';
import { useDispatch } from 'react-redux';

const useStyles = makeStyles(theme => ({
  cell: {
    zIndex: theme.zIndex.mobileStepper,
  },
  disableCell: {
    background: `linear-gradient(-45deg,${theme.palette.action.disabledBackground} 10%,transparent 10%,transparent 50%,${theme.palette.action.disabledBackground} 50%,${theme.palette.action.disabledBackground} 60%,transparent 60%,transparent)`,
    backgroundSize: '8px 8px',
    pointerEvents: 'none',
  },
}));

const TimeTableCell = (props: Object): React.Element<'div'> => {
  const classes = useStyles();
  const {
    startDate: dateString,
    timeConfig,
    onDoubleClick,
    ...restProps
  } = props;
  const dispatch = useDispatch();

  const onMouseDown = (event: MouseEvent) => {
    event.preventDefault();
    dispatch(beginSelectingCell(dateString));
  };

  const onMouseEnter = (event: MouseEvent) => {
    event.preventDefault();
    dispatch(addSelectedCell(dateString));
  };

  const onMouseUp = (event: MouseEvent) => {
    event.preventDefault();
    onDoubleClick();
  };

  return (
    <WeekView.TimeTableCell
      {...restProps}
      startDate={dateString}
      id={dateString.toString()}
      onMouseDown={onMouseDown}
      onMouseEnter={onMouseEnter}
      onMouseUp={onMouseUp}
      className={clsx(
        classes.cell,
        {
          [classes.disableCell]: !isCellActive({
            dateString,
            config: timeConfig,
          }),
        },
        CELL_CLASS,
      )}
    />
  );
};

const structuredSelector = createSelector(
  state => _get(state, 'calendar.config.times'),
  state => _get(state, 'calendar.config.timeZone'),
  (timeConfig, timeZone) => ({
    timeConfig: { ...timeConfig, timeZone },
  }),
);

type PropsWithoutRedux = {};
export default (connect(structuredSelector)(
  withErrorBoundary(React.memo(TimeTableCell)),
): React.AbstractComponent<PropsWithoutRedux>);
